import {
    Component,
    OnInit
} from '@angular/core';

@Component({
    selector: 'profile',
    styles: [require('./profile.component.css')],
    template: require('./profile.component.html')
})
export class ProfileComponent implements OnInit {
    private profile: any;
    constructor() {
        this.profile = JSON.parse(localStorage.getItem('profile'));
    }
    public ngOnInit() {
    }
}


